body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;font-size:14px}#root{overflow:auto}body{position:fixed;overflow:hidden;overscroll-behavior-y:none;font-family:-apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;color:black;-webkit-font-smoothing:antialiased;background:#f1f4f8}header{position:absolute;height:108px;width:100%;padding:24px 48px;display:flex;align-items:center;justify-content:space-between;color:#2a2a24}header .logo span,header .episode span{font-size:1rem;font-weight:700;letter-spacing:0.04rem}

